
$page-top: 100px;

$minutes-hand-height: 150px;
$minutes-hand-width: 8px;
$minutes-hand-border-radius: 4px;
$minutes-hand-color: #c92b2b;

$seconds-hand-height: 200px;
$seconds-hand-width: 6px;
$seconds-hand-border-radius: 3px;
$seconds-hand-color: #4169e1;

$center-size: 20px;
$center-color: $seconds-hand-color;
$center-shift: $seconds-hand-height + $page-top - ($center-size / 2);
